body {
    font-family: Arial, sans-serif;
    background-color: #ffffff;
    color: #273b42;
}

header {
    display: flex;
    flex-direction: column;
    align-items: center;  /* Center both the logo and navigation */
    justify-content: center;
    background-color: #253e42;
    color: #ffffff;
    padding: 20px;  /* Increase padding for spacing */
    text-align: center;
}

nav ul {
    list-style: none;
    display: flex;
    justify-content: center;
    padding: 0;
    margin: 0;
}

nav ul li {
    margin: 0 1.5em;  /* Add more space between nav items */
}

nav ul li a {
    color: #fdfdfd;
    text-decoration: none;
    font-size: 18px;  /* Slightly larger font size for visibility */
    font-weight: bold;
}

nav ul li a:hover {
    color: #bdc3c7;  /* Change hover color */
}

section {
    padding: 2em;
}

footer {
    background-color: #263d43;
    color: #ffffff;
    text-align: center;
    padding: 1em;
}

/* Centering the logo in the header or footer */
header img, footer img {
    display: block;
    margin: 0 auto;
    padding: 10px 0;
    width: 150px;  /* Set consistent width for logo */
}

header img {
    max-width: 100%;  /* Make the logo responsive */
    height: auto;
}

textarea {
    width: 100%; /* Full width of the parent container */
    height: 30vh; /* 30% of the viewport height */
    resize: none; /* Prevent the user from resizing the textarea */
    box-sizing: border-box; /* Include padding and border in the element's total width and height */
    padding: 10px;
}